body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.orderItem {
	display: block;
	padding: 11px 10px 11px 15px;
}

.orderItem_picContainer {
	padding-right: 10px;
	display: inline-block;
}

.orderItem_pic {
	width: 90px;
	height: 90px;
}

.orderItem_content {
	display: inline-block;
	width: calc(100% - 100px)
}

.orderItem_product {
	max-width: 237px;
	font-size: 17px;
	font-weight: 700;
	color: #111;
	padding-right: 8px;
	box-sizing: border-box;
	overflow: hidden;
	text-overflow: ellipsis;
	white-space: nowrap;
}

.orderItem_shop {
	overflow: hidden;
	text-overflow: ellipsis;
	white-space: nowrap;
	font-size: 13px;
	color: #777;
	padding-top: 9px;
	padding-bottom: 13px;
}

.orderItem_detail {
	display: flex;
	justify-content: space-between;
	height: 22px;
	line-height: 22px;
	padding-right: 4px;
}

.orderItem_price {
	font-size: 13px;
	color: #777;
	vertical-align: 1px;
	margin-left: 10px;
}

.orderItem_price::before {
	content: "\A5";
	margin-right: 1px;
}

.orderItem_btn {
	width: 80px;
	height: 22px;
	text-align: center;
	color: #fff;
	border: 0;
	font-size: 14px;
	cursor: pointer;
}
.orderItem_btn--gray {
	background-color: #999;
}
.orderItem_btn--red {
	background-color: #e9203d;
	margin-right: 15px;
}
.orderItem_commentContainer {
	width: 100%;
}

.orderItem_comment{
	width: 100%;
	height: 80px;
	border: 1px solid #f1f1f1;
	box-sizing: border-box;
	margin-top: 10px;
	resize: none;
	text-indent: 1em;
}
.orderItem_starContainer {
	margin-bottom: 5px;
}

.orderItem_star {
	color: #999;
	font-size: 20px;
}

.orderItem_star--light {
	color: rgb(233, 32, 61);
	font-size: 20px;
}

.myOrder {
	background-color: #e9203d;
	color: #fff;
	padding: 15px 10px;
	text-align: center;
	font-size: 16px;
	line-height: 1;
}
.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

